import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import { useTheme } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Unstable_Grid2';

import { useResponsive } from 'src/hooks/use-responsive';

import { useTranslate } from 'src/locales';
// redux
import { useSelector, useDispatch } from 'src/redux/store';
import Image from 'src/components/image';
import { getGames, getPromotes, getCategories } from 'src/redux/slices/generale';

import { useSettingsContext } from 'src/components/settings';
import { LoadingScreen } from 'src/components/loading-screen';
import { paths } from 'src/routes/paths';

import AppWinner from '../app-winner';
import AppPromotion from '../app-promotion';
import GameTopNavBar from '../game-top-nav';
import GameSecondaryNavBar from '../game-secondary-bar';

// ----------------------------------------------------------------------

export default function OverviewAppView() {
  const { t } = useTranslate();

  const { promotes, games, categories } = useSelector((state) => state.generale);

  const dispatch = useDispatch();

  const [loading, setLoading] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getPromotes());
    dispatch(getGames());
    dispatch(getCategories());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const theme = useTheme();
  const isDesktop = useResponsive('up', 'md');
  const lgUp = useResponsive('up', 'lg');
  const settings = useSettingsContext();

  const isVertical = settings.themeLayout === 'vertical';

  const renderMainContent = () => {
    if (loading) {
      return (
        <Stack sx={{ py: 8 }}>
          <LoadingScreen />
        </Stack>
      );
    }
    const content_title = (
      <>
        <AppPromotion list={promotes} borderRadius={isDesktop ? 1.5 : 0} />
      </>
    );

    return (
      <>
        {content_title}
        {!lgUp && (
          <Stack>
            <GameTopNavBar list={categories} />
            <GameSecondaryNavBar list={categories} />
          </Stack>
        )}
        <Box
          display="grid"
          gridTemplateColumns={{
            xs: 'repeat(2, 1fr)',
            sm: 'repeat(2, 1fr)',
            md: 'repeat(3, 1fr)',
            lg: 'repeat(4, 1fr)',
          }}
          gap={1.5}
          justifyContent="center"
          alignItems="center"
          sx={{ px: isDesktop ? 0 : 2, py: isDesktop ? 0 : 2 }}
        >
          {games.map((game: any) => (
            <GameItem key={game.id} game={game} />
          ))}
        </Box>
      </>
    );
  };

  return (
    <Container
      maxWidth={isDesktop ? false : 'xl'}
      sx={{ ...(!isDesktop && { px: '0px !important' }) }}
    >
      <Grid container>
        {isDesktop ? (
          <>
            <Grid xs={12} md={!lgUp ? 12 : 9}>
              <Stack spacing={2}>{renderMainContent()}</Stack>
            </Grid>
            {lgUp && (
              <Grid xs={12} md={3}>
                <Stack spacing={2}>
                  <AppWinner
                    title={t('betslip.last_winner')}
                    img="/static/assets/images/jeton.png"
                  />
                </Stack>
              </Grid>
            )}
          </>
        ) : (
          <Grid xs={12}>{renderMainContent()}</Grid>
        )}
      </Grid>
    </Container>
  );
}

function GameItem({ game }: any) {
  const { id, name, image } = game;
  const navigate = useNavigate();

  const handlePlay = () => {
    navigate(paths.casino.game_view(id));
  };

  return (
    <Box
      onClick={handlePlay}
      sx={{
        cursor: 'pointer',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <Image
        alt={name}
        src={image}
        ratio="4/3"
        sx={{
          borderRadius: 1.5,
        }}
      />
    </Box>
  );
}
