import { Box, Stack, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Iconify from 'src/components/iconify';
import { CardProps } from '@mui/material/Card';
import { useRef, useEffect, useState } from 'react';

type GameCategoryItem = {
  id: number;
  name: string;
  icon: string;
  is_active: boolean;
  route: string;
};

interface Props extends CardProps {
  list: GameCategoryItem[];
}

export default function GameTopNavBar({ list }: Props) {
  const navigate = useNavigate();
  const scrollRef = useRef<HTMLDivElement>(null);
  const [canScrollLeft, setCanScrollLeft] = useState(false);
  const [canScrollRight, setCanScrollRight] = useState(false);

  const updateScrollArrows = () => {
    const el = scrollRef.current;
    if (el) {
      setCanScrollLeft(el.scrollLeft > 0);
      setCanScrollRight(el.scrollLeft + el.clientWidth < el.scrollWidth);
    }
  };

  const scroll = (direction: 'left' | 'right') => {
    if (scrollRef.current) {
      scrollRef.current.scrollBy({
        left: direction === 'left' ? -100 : 100,
        behavior: 'smooth',
      });
    }
  };

  useEffect(() => {
    updateScrollArrows();
    window.addEventListener('resize', updateScrollArrows);
    const el = scrollRef.current;
    el?.addEventListener('scroll', updateScrollArrows);
    return () => {
      window.removeEventListener('resize', updateScrollArrows);
      el?.removeEventListener('scroll', updateScrollArrows);
    };
  }, []);

  const background = '#f5f6f8';
  const textColor = '#2b2b2b';

  return (
    <Box sx={{ position: 'relative', bgcolor: background, overflow: 'hidden' }}>
      {/* Flèche gauche */}
      {canScrollLeft && (
        <Box
          onClick={() => scroll('left')}
          sx={{
            position: 'absolute',
            left: 0,
            top: 0,
            bottom: 0,
            width: 48,
            zIndex: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            background: 'linear-gradient(to right, rgba(245, 246, 248, 1), rgba(245, 246, 248, 0))',
            cursor: 'pointer',
          }}
        >
          <Iconify icon="eva:arrow-ios-back-fill" width={24} sx={{ color: textColor }} />
        </Box>
      )}

      {/* Liste défilante des catégories */}
      <Box
        ref={scrollRef}
        sx={{
          display: 'flex',
          overflowX: 'auto',
          scrollbarWidth: 'none',
          '&::-webkit-scrollbar': { display: 'none' },
          py: 1.5,
        }}
      >
        {list
          ?.filter((item) => item.is_active)
          ?.map((item) => (
            <Stack key={item.id} alignItems="center" spacing={0.5} sx={{ minWidth: 50, mx: 0.5 }}>
              <Box
                onClick={() => navigate(item.route)}
                sx={{
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  transition: 'transform 0.2s ease-in-out',
                  '&:hover img': {
                    transform: 'scale(1.1)',
                  },
                }}
              >
                <Box
                  component="img"
                  src={item.icon}
                  alt={item.name}
                  sx={{
                    width: 36,
                    height: 36,
                    transition: 'transform 0.2s ease-in-out',
                  }}
                />
              </Box>
              <Typography
                variant="caption"
                sx={{ fontSize: 12, textAlign: 'center', color: textColor }}
              >
                {item.name}
              </Typography>
            </Stack>
          ))}
      </Box>

      {/* Flèche droite */}
      {canScrollRight && (
        <Box
          onClick={() => scroll('right')}
          sx={{
            position: 'absolute',
            right: 0,
            top: 0,
            bottom: 0,
            width: 48,
            zIndex: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            background: 'linear-gradient(to left, rgba(245, 246, 248, 1), rgba(245, 246, 248, 0))',
            cursor: 'pointer',
          }}
        >
          <Iconify icon="eva:arrow-ios-forward-fill" width={24} sx={{ color: textColor }} />
        </Box>
      )}
    </Box>
  );
}
